import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import Location from './components/location';
import CurrentLocation from './components/current_location';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

function Home() {
   return (
     <div className="Home">
       <App />
     </div>
   );
  }

  function Daily() {
   return (
     <div className="daily">
       <h2>
         <Location />
       </h2>
     </div>
   );
  }

  function Hourly() {
    return (
      <div className="hourly">
        <h2>
          <CurrentLocation />
        </h2>
      </div>
    );
   }
  function AppRouter() {
   return (
     <Router>
       <div>
         <nav>
           <div className = "navigation">
             
               <button><Link className="list" to="/">Home</Link></button>
             
            
                <button><Link className="list" to="/Daily/">Daily Forecast</Link></button>
             
                <button><Link className="list" to="/Hourly/">Hourly Forecast</Link></button>
             
           </div>
         </nav>
                <Route exact path="/"  component={Home} />
                <Route path="/Daily" component={Daily} />
                <Route path="/Hourly" component={Hourly} />
       </div>
     </Router>
   );
  }

ReactDOM.render(<AppRouter />, document.getElementById('root'));